@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border:0;
}

body, html{
  height: 100%;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
}

.container{
  max-width: 980px;
  padding: 0 15px;
  height: 100%;
  margin: 0 auto;
}


:root::-webkit-scrollbar {
  width: 0.5em;
}

:root::-webkit-scrollbar-track {
  background: rgba(255,255,255,0.4);
  border-radius: 3px;
}

:root::-webkit-scrollbar-thumb {
  background-color: #6284FF;
  background-image: linear-gradient(0deg, #6284FF 0%, #FF0000 100%);
  
  border-radius: 3px;
}
.home-content{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.home__image-box{
    flex: 1 0 50%;
}

.home__image{
    display: block;
    max-width: 100%;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    object-fit: cover;
}

.home__text-box{
    flex: 1 0 50%;
    padding: 60px 20px 30px 30px;
    background: rgba( 255, 255, 255, 0.20 );
    backdrop-filter: blur( 12.0px );
    -webkit-backdrop-filter: blur( 12.0px );
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}

.home__title{
    font-size: 2.5em;
    font-weight: 300;
    margin-bottom: 20px;
}

.home__sub-title{
    color: #fff;
    font-size: 1.5em;
    font-weight: 300;
}
.posts-row{
    flex-direction: column;
    align-items: center;
}
.post{
    max-width: 680px;
    display: flex;
    padding: 15px;
    margin-bottom: 10px;
}

.post__avatar-image{
    display: block;
    flex: 0 0 50px;
    max-width: 50px;
    height: auto;
    object-fit: cover;
    border-radius: 50%;
}

.post__inner{
    flex-grow: 1;
    flex-shrink: 0;
    flex-basis: calc(100% - 50px); 
    padding-left: 15px;
    display: flex;
    flex-direction: column;
}

.post__header{
    display: flex;
    justify-content: space-between;
}

.post__info{
    display: flex;
    align-items: center;
    justify-self: flex-start;
}

.post__info span{
    margin-right: 10px;
    line-height: 1.2;
}

.author-name{
    color:#fff;
    font-weight: 700;
}

.author-nickname{
    position: relative;
    margin-right: 15px !important;
}

.author-nickname::before{
    content: '';
    width: 2px;
    height: 2px;
    background-color:  rgba(255,255,255,0.7);
    border-radius: 50%; 
    top: 50%;
    right: -8px;
    position: absolute;
}

.author-nickname, .post__date{
    color: rgba(255,255,255,0.7);
}

.post__body{
    display: flex;
    flex-direction: column;
}

.post__text{
    margin-top: 10px;
    line-height: 1.2;
    color:#fff;
}

.post__image{
    margin-top: 10px;
    display: block;
    max-width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 10px;
}

.svg-icon{
    width: 16px;
    height: auto;
}

.svg-icon.approved{
    margin-left: 5px;
}

.post__button{
    color:rgba(255,255,255,0.7);
    font-size: 1.2rem;
    background: transparent;
    cursor: pointer;
}

.post__footer{
    padding-top: 15px;
    display: flex;
    flex-wrap: wrap;
}

.post__column-4{
    flex: 1 0 25%;
}

.action__info{
    margin-left: 10px;
    line-height: 1.2;
    color:rgba(255,255,255,0.7);
}
.photo{
    flex: 1 0 50%;
    padding: 0 10px 10px 0;
}

.image{
    max-width: 100%;
    object-fit: cover;
}
.contact-link{
    text-decoration: none;
    display: block;
    margin-bottom: 5px;
    padding: 10px;
    border-radius: 10px;
}

.contact-link:hover{
    transition: 0.3s ease-in;
    background-color: rgba(255, 255, 255, 0.2);
}

.contact-link--active{
    background-color: rgba(255, 255, 255, 0.2);
}

.contact-info--additional{
    color:#fff;
    font-size: 0.9em;
    font-weight: 400;
}

.info-line{
    margin: 5px 0;
    text-align: left;
}

.info-category{
    margin-right: 5px;
    font-style: italic;
}

.contact-inner{
    display: flex;
    flex-wrap: wrap;
}

.contact-icon{
    flex: 1 0 20%;
}

.contact-icon__image{
    max-width: 100%;
    height: auto;
    object-fit: cover;
}

.contact-info{
    flex: 1 0 80%;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.contact-info__box{
    color: #fff;
    font-weight: 300;
    font-size: 1.2em;
}

.contact-phone{
    font-size: 0.9em;
}

.contact-fullname{
    margin-bottom: 5px;
    font-style: italic;
}

.first-name{
    margin-right: 5px;
}

.filter-form {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin-bottom: 15px;
}

.input-box {
    margin-top: 10px;
    width: 100%;
    display: flex;
    align-items: center;
}

.filter-form__input {
    width: 100%;
    padding: 10px 20px;
    border-radius: 5px;
    border: 3px solid rgba(255, 255, 255, 0.5);
    background: rgba(255, 255, 255, 0.2);
    outline: none;
    color: #fff;
    font-weight: 400;

}

.filter-form__input::-webkit-input-placeholder {
    font-style: italic;
    color: #fff;
    font-weight: 200;
    font-size: 1.2em;
}

.filter-form__input:-ms-input-placeholder {
    font-style: italic;
    color: #fff;
    font-weight: 200;
    font-size: 1.2em;
}

.filter-form__input::placeholder {
    font-style: italic;
    color: #fff;
    font-weight: 200;
    font-size: 1.2em;
}

.checkbox-block {
    margin-right: 20px;
}

.filter-form__label {
    display: inline-flex;
    align-items: center;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.label-img{
    width: 20px;
    height: 20px;
}

.filter-form__checkbox {
    position: absolute;
    z-index: -1;
    opacity: 0;
}

.filter-form__checkbox + .filter-form__label::before {
    content: '';
    display: inline-block;
    width: 1.2em;
    height: 1.2em;
    flex-shrink: 0;
    flex-grow: 0;
    border: 1px solid #c867db;
    background:#fff;
    border-radius: 0.25em;
    margin-right: 0.5em;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
}

.filter-form__checkbox:checked + .filter-form__label::before {
    border-color: #7C38C9;
    background-color: #7C38C9;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
}

.filter-form__checkbox:not(:disabled):not(:checked) + .filter-form__label:hover::before {
    border-color: #c867db;
}

.filter-form__checkbox:not(:disabled):active + .filter-form__label::before {
    background-color: #c867db;
    border-color: #c867db;
}

.filter-form__checkbox:focus + .filter-form__label::before {
    box-shadow: 0 0 0 0.2rem rgba(124,56,201, 0.25);
}

.filter-form__checkbox:focus:not(:checked) + .filter-form__label::before {
    border-color: #c867db;
}

.filter-form__checkbox:disabled + .filter-form__label::before {
    background-color: #e9ecef;
}
.contacts-box {
    background: rgba(124, 56, 201, 0.50);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
    backdrop-filter: blur(10.5px);
    -webkit-backdrop-filter: blur(10.5px);
    border-radius: 10px;
    padding: 30px 20px 15px 20px;
    max-width: 560px;
    margin: 0 auto;
}

.contacts-title {
    font-size: 1.5em;
    font-weight: 700;
    color: #222;
    line-height: 1.5;
    margin: 0 0 15px 0;
    color: #fff;
    font-weight: 400;
}

.contacts-list {
    display: flex;
    flex-direction: column;
}

.message{
    height: 100%;
    padding-top: 50px;
}

.message__title{
    text-align: center;
    color: #fff;
    font-size: 1.5em;
    font-weight: 400;
    font-style: italic;
}
