.wrapper{
    min-height: 100vh;
    background: url(/cursor-react-router/static/media/wrapper-background.c137bb90.jpg) no-repeat center / cover fixed;
}  

.menu{
    height: 80px;
    width: 100%;
    background: rgba( 255, 255, 255, 0.2);
    backdrop-filter: blur( 4px );
    -webkit-backdrop-filter: blur( 4px );
}

.menu-inner{
    max-width: 980px;
    margin: 0 auto;
    padding: 0 15px;
    height: 100%;
    display: flex;
    align-items: center;
}

.menu-list{
    display: flex;
    list-style: none;
}

.menu-list__item{
    margin-right: 10px;
}

.menu__link{
    display: inline-block;
    min-width: 120px;
    text-decoration: none;
    padding: 10px 15px;
    font-size: 1.3em;
    color: #fff;
    font-weight: 600;
    position: relative;
}

.menu__link::after{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    height: 3px;
    width: 100%;
    border-radius: 3px;
    background-color: #fff;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
}

.menu__link:hover::after{
    transition: 0.3s ease-in-out;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}

.menu__link--active::after{
    transition: 0.3s ease-in-out;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}

.section{
    padding: 40px 10px 30px 10px;
}

.content-row{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
}

.section__title, .home__title{
    color: #fff;
    position: relative;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
} 

.section__title{
    font-size: 3em;
    font-weight: 200;
    letter-spacing: 3px;
    margin-bottom: 40px;
}

.home__title::before, .section__title::before{
    content:'';
    display: block;
    width: 100%;
    height: 3px;
    position: absolute;
    left: 0;
    bottom: -10px;
    background: rgba(255,255,255,0.6);
}

